      isUploading: false,
      showServer: 0,
      cutOff: {},
      miniDashboard: {},
      activeStatusFilter: null,
      maxUploadSize: 60,
      errMessage: false,
      selectedAgenda: { label: 'RKAP Kualitatif', code: null },
      agendaList: []
    }
  },
  computed: {
    isValidFilter () {
      return !!(this.usedFilter.periode_year && this.usedFilter.division_id)
    }
  },
  watch: {
    searchFilter: {
      deep: false,
      immediate: false,
      handler (e) {
        this.usedFilter = Object.assign({}, this.usedFilter, {
          search: e,
          page: 1,
          limit: 10
        })
      }
    },
    selectedAgenda: {
      deep: true,
      immediate: false,
      handler (e) {
        const res = (typeof e === 'object') ? e.code : e
        this.usedFilter = Object.assign({}, this.usedFilter, {
          agenda_id: res,
          page: 1,
          limit: 10
        })
      }
    },
    activeStatusFilter: {
      deep: false,
      immediate: false,
      handler (e) {
        this.usedFilter = Object.assign({}, this.usedFilter, {
          status_code_data_verification: e,
          page: 1,
          limit: 10
        })
      }
    },
    usedFilter: {
      deep: true,
      immediate: false,
      handler (e) {
        this.getListData(e)
      }
    },
    logPage: {
      deep: true,
      immediate: false,
      handler (e) {
        this.getLogTemplate(this.activeTemplate, e)
      }
    }
  },
  methods: {
    globalFilterChange (e) {
      this.fullFilter = Object.assign({}, e)
      this.getAgenda(e, null, true)
    },
    getAgenda (e, selected = null, fromGlobal = false) {
      const tempFilter = Object.assign({}, {
        division_id: fromGlobal ? e.division_id && e.division_id.code ? e.division_id.code : null : e.division_id,
        periode_year: fromGlobal ? e.year : e.periode_year,
        agenda_id: selected || null
      })
      this.$_api.dataset('m-qualitatif-agendas', tempFilter).then(ag => {
        let agenda = ag.data.map(x => { return { label: x.name, code: x.id } })
        this.agendaList = [...[{ label: 'RKAP Kualitatif', code: null }], ...agenda]
        if (!selected) this.selectedAgenda = this.agendaList[0]
        else this.selectedAgenda = this.agendaList.filter(x => x.code === selected)[0]
        // ======
        this.usedFilter = Object.assign({}, tempFilter, {
          search: '',
          page: 1,
          limit: 10,
          id: this.$route.query.id || null
        })
        this.activeTemplate = Object.assign({}, {})
        this.activeAttachment = Object.assign({}, {})
        this.getCutOff(this.usedFilter)
      })
    },
    removeMyAction () {
      let query = Object.assign({}, this.$route.query)
      delete query.id
      this.$router.replace({ query })
    },
    getCutOff (e) {
      this.$_api.get('custom/cut-off-qualitatif-schedules/closing-date', e).then(res => {
        this.cutOff = res.data
      }, err => {
        this.$_alert.error(err)
      })
    },
    getMiniDashboard (e) {
      this.$_api.get('custom/qualitatif-data-on-divisions/mini-dashboard', e).then(res => {
        this.miniDashboard = res.data
      }, err => {
        this.$_alert.error(err)
      })
    },
    getListData (e, renewTemplate = false) {
      this.isLoading = true
      this.$_api.get('custom/qualitatif-data-on-divisions/list', e).then(res => {
        this.isLoading = false
        this.dataList = res
        if (renewTemplate) {
          const newVal = res.data.filter(x => x.id === this.activeTemplate.id)
          if (newVal.length) this.activeTemplate = Object.assign(newVal[0])
          else this.activeTemplate = Object.assign({})
        } else {
          if (res.data.length) this.showTemplate(res.data[0])
          else this.activeTemplate = Object.assign({})
        }
        this.getMiniDashboard(e)
      }, err => {
        this.isLoading = false
        this.$_alert.error(err)
      })
    },
    showTemplate (e) {
      this.activeTemplate = Object.assign(e)
      if (this.activeAttachment.id !== e.id) this.getLogTemplate(e, 1)
    },
    showAtachment (e) {
      if (this.activeAttachment.id === e.id) this.activeAttachment = Object.assign({})
      else this.activeAttachment = Object.assign(e)
      this.$bvModal.show('modal-custom-preview-doc')
    },
    getLogTemplate (e, page) {
      const filter = { ...{ template_qualitatif_on_division_id: e.id, periode_year: this.usedFilter.periode_year }, ...{ page: page, limit: 5 } }
      this.$_api.get('qualitatif-data-on-division-log-verifications/list', filter).then(res => {
        this.logData = res
        this.logTemp = res.data.slice(Math.max(res.data.length - 2, 0))
      })
    },
    handleFile (e) {
      if ((e.target.files[0].size / 1000000).toFixed(2) < this.maxUploadSize) {
        this.isUploading = true
        let file = e.target.files[0]
        this.$_api.fileUpload(file, event => {
          this.uploadPercentage = Math.round((100 * event.loaded) / event.total)
        }).then(res => {
          this.isUploading = false
          this.addNewAttachment(res.data)
          e.target.value = null
        }).catch((err) => {
          this.isUploading = false
          this.$_alert.error(err)
        })
      } else {
        this.errMessage = true
        e.target.value = null
      }
    },
    addNewAttachment (e) {
      this.isLoading = true
      const form = {
        template_qualitatif_on_division_id: this.activeTemplate.id,
        file_document: e
      }
      this.$_api.create('qualitatif-data-on-division-attachments', form).then(async res => {
        this.isLoading = false
        await this.getListData(this.usedFilter, true)
        this.getMiniDashboard(this.usedFilter)
      }, err => {
        this.isLoading = false
        this.$_alert.error(err)
      })
    },
    deleteAttachment (e) {
      this.$_alert.confirm('Hapus Data Input ?', 'Data yang sudah dihapus tidak dapat dikembalikan, lanjutkan ?').then((result) => {
        if (result.isConfirmed) {
          this.$_api.delete('qualitatif-data-on-division-attachments', { data: e }).then(async res => {
            this.isLoading = false
            await this.getListData(this.usedFilter, true)
            this.getMiniDashboard(this.usedFilter)
          }, err => {
            this.isLoading = false
            this.$_alert.error(err)
          })
        }
      })
    },
    allowManipulate (e) {
      if (Object.keys(this.cutOff).length && this.cutOff.is_pass) return false
      else {
        if (this.$_sys.isAllowed(this.config.permission.update)) {
          if (e.status_code_data_verification === 'draft') return true
          else if (e.status_code_data_verification === 'revision_request') return true
          else if (e.status_code_data_verification === null) return true
          else return false
        } else return false
      }
    },
    verifyItems (e) {
      this.$_alert.confirm('Pengajuan Verifikasi Data', 'Anda tidak dapat mengupload dan menghapus file setelah diajukan, Lanjutkan?').then((result) => {
        if (result.isConfirmed) {
          this.$_api.post('custom/qualitatif-data-on-divisions/do-verification-request', e).then(async res => {
            this.isLoading = false
            this.getListData(this.usedFilter, true)
            this.getMiniDashboard(this.usedFilter)
            this.getLogTemplate(e, 1)
          }, err => {
            this.isLoading = false
            this.$_alert.error(err)
          })
        }
      })
    },
    isAllowedVerify (data) {
      const profile = JSON.parse(localStorage.getItem('profile'))
      if (profile.role_id === -1) return true
      else {
        return profile.role_id === data.privillege_role_id
      }
    },
    generateText (data) {
      let text = 'Telah diverifikasi oleh, ' + data.rel_verified_by
      text += '\nSebagai ' + data.rel_privillege_role_id
      text += '\npada ' + moment(data.verified_by).format('LLLL')
      text += '\n\nvia ' + window.location.origin
      return text
    },
    approveItem (e = true, s) {
      this.$swal({
        title: e ? 'Approve Data Upload ?' : 'Permintaan Revisi',
        html: `<span class="text-dark-75">Keterangan&nbsp;:</span>`,
        input: 'textarea',
        icon: e ? 'success' : 'warning',
        iconColor: e ? '#3AAC12' : '#d42b21',
        padding: '.75rem',
        width: '37rem',
        focusConfirm: false,
        showCancelButton: false,
        confirmButtonText: e ? 'Approve Data Upload' : 'Kirim Permintaan Revisi',
        customClass: {
          confirmButton: e ? 'btn btn-success' : 'btn btn-light-danger',
          actions: 'mt-0 mb-5',
          htmlContainer: 'text-left overflow-hidden'
        },
        didOpen: () => {
          this.$swal.disableButtons()
          this.$swal.getInput().addEventListener('keyup', (e) => {
            if (e.target.value) {
              this.$swal.enableButtons()
            } else {
              this.$swal.disableButtons()
            }
          })
        }
      }).then((result) => {
        if (result.isConfirmed) {
          this.sendAprroval(e, s, this.$swal.getInput().value)
        }
      })
    },
    sendAprroval (e, data, desc = '') {
      const form = { ...data, ...{ status_code: e ? 'approved' : 'revision_request', description: desc } }
      this.$_api.post('custom/qualitatif-data-on-divisions/do-verify', form).then(async res => {
        this.isLoading = false
        await this.getListData(this.usedFilter, true)
        this.getLogTemplate(this.activeTemplate, 1)
        this.getMiniDashboard(this.usedFilter)
      }, err => {
        this.isLoading = false
        this.$_alert.error(err)
      })
    }
  }
}
</script>

<style>
  .popover-xl {
    max-width: 400px !important;
    width: 400px !important;
  }
  .rotate-90 {
    transform: rotate(90deg);
  }
  .swal2-textarea {
    margin-top: 0.25rem;
    -webkit-tap-highlight-color: transparent;
  }
  .swal2-textarea:focus {
    outline: none !important;
    box-shadow: none !important;
  }
  .mini-recapt {
    display: flex;
    min-width: 200px;
    justify-content: space-between;
    align-items: center;
    padding: 0.75rem 1rem;
    border: 1px solid #ebedf3 !important;
    border-radius: 8px;
    cursor: pointer;
  }
  .mini-recapt:not(:last-of-type) {
    margin-right: 0.5rem;
  }
  .mini-recapt.active {
    background: var(--primary);
    color: #fff;
  }
  .img-unverified {
    width: 75px;
  }
</style>



